        </BCol>
      </BRow>
      <BRow>
        <BCol>
          <!--  -->
          <BFormSpinbutton
            class="gear-spin"
            v-model="gearSubL"
            min="0"
            :max="gearLLimit"
            size="lg"
            inline
          ></BFormSpinbutton>
        </BCol>
        <BCol>
          <!--  -->
          <BFormSpinbutton
            class="gear-spin"
